import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'

Vue.use(Router)

export default new Router({
	base: 'SteamShelf',
	routes: [
		{
			path: '/',
			name: 'Hello',
			component: Hello
		}
	]
});
console.log(this);



